body {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  background-image: url(./pexels-suzy-hazelwood-7026467.jpg);
  /* background: #f5f5f5; */
}

img {
  width: auto;
  height: 100px;
}
table {
  margin: auto;
  background: white;
  border-collapse: collapse;
}

td {
  display: flex;
  min-width: 100px;
  min-height: 104px;
  justify-content: space-around;
  align-items: center;
  border: 1px black solid;
}

tbody {
  display: flex;
}

/* shiny list styles */
.photoWrapper {
  position: relative;
  height: 100px;
  width: 100px;
  margin: 0 auto;
}

.photoWrapper,
p {
  margin: 5px;
}

.pokemonCard {
  display: inline-block;
  margin: 5px;
  border: 1px solid black;
  background: #f5f5f5;
  padding: 5px;
  border-radius: 1rem;
}

.sprites {
  position: absolute;
  top: 0;
  left: 0;
}

.shiny {
  z-index: 10;
}

.types {
  display: inline-flex;
  width: 92%;
  justify-content: space-evenly;
  width: 59px;
  height: 19px;
}

.scroll {
  overflow: scroll;
  max-width: 90%;
  margin: auto;
}

/* single info */
.single-info {
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}

.heading-section {
  background: #67c5ff;
  margin: 0 auto;
  padding-top: 1rem;
}
.tab-button {
  width: 50%;
  border: none;
}

/* Shiny List */
.shiny-list-wrapper {
  width: 100%;
  margin: auto;
  background: rgba(197, 197, 197, 0.2);
  padding-top: 1rem;
}
.shiny-list-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: center;
}

/* Top button */
.top-button {
  position: sticky;
  bottom: 5px;
  right: 5px;
  float: right;
  display: block;
  height: 3rem;
  width: 3rem;
  padding: 15px 5px 5px;
  background: #67c5ff;
  border: none;
}
.arrow {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-top: 2px solid #fff;
  border-right: 2px solid #fff;
  transform: rotate(315deg);
}

/* main button style */
.btn {
  display: inline-block;
  padding: 0.3em 1em;
  text-decoration: none;
  color: #67c5ff;
  border: solid 2px #67c5ff;
  border-radius: 3px;
  transition: 0.4s;
}

.btn:hover {
  background: #67c5ff;
  color: white;
}
/* search bar */
.search {
  width: 100%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 3px solid #67c5ff;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9dbfaf;
}

.searchTerm:focus {
  color: #00b4cc;
}

.searchButton {
  border: 1px solid #67c5ff;
  background: #67c5ff;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.wrap {
  width: 50%;
  margin: 1rem auto;
  display: flex;
}
/* hide scrollbars */

.scroll {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.scroll {
  overflow-y: hidden;
  overflow-x: visible;
  -ms-overflow-style: revert;
  scrollbar-width: revert;
}

@media screen and (min-width: 770px) {
  img {
    width: auto;
    height: 200px;
  }
  .photoWrapper {
    height: 200px;
    width: 200px;
  }

  td,
  tr {
    display: flex;
    border-collapse: collapse;
  }
  td {
    min-width: 200px;
    min-height: auto;
  }
  tbody {
    display: inline-block;
  }
  .tr-gen {
    max-height: 30px;
  }
}

@media screen and (max-width: 770px) {
  .scroll::-webkit-scrollbar {
    display: none;
  }
}
